.root {
  position: fixed;
  inset: 0;
  border: none;
  padding: 0;
  width: 100%;
  display: grid;
  z-index: 10;
  transition: opacity 0.3s ease-in-out;

  &:not([open]) {
    pointer-events: none;
    opacity: 0;
  }

  &::backdrop {
    background-color: rgb(0 0 0 / 0.3);
    backdrop-filter: blur(7px);
  }
}

.wrapper {
  padding: 65px 1em;
}

.popupContainer {
  width: 1160px;
  height: 773px;
  border: 2px solid #d6d6d6;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.popupImage {
  width: 490px;
  height: 633px;
  align-self: center;
  margin: 0 auto;
}

.closeButton {
  background-color: transparent;
  background-image: url('../../assets/icons/prep-materials-popup-close-icon.svg');
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  width: 22.5px;
  height: 22.5px;
  margin: 37px 37.18px 0 0;
  padding: 0;
  align-self: flex-end;
}
